.about-text {
    position: absolute;
    
    max-width: 600px;
    width: 85%;

    top: calc(25px + 2.5vh);
    left: 50%;
    padding-bottom: 75px;

    transform: translate(-50%,0);

}

.about-text .title {
    color: var(--dark);
    font-weight: 600;
    font-size: calc(20px + 2vmin);
}

.about-text .body {
    color: #666666;
    font-size: calc(14px + 1vmin);
    line-height: calc(16px + 1.5vmin);
}